const suspendedEffects: Function[] = [];

export function effect(fn: Function) {
  const iterator = effectGenerator(fn);
  function run() {
    const result = iterator.next();
    if (!result.done) {
      // Effect is suspended, store for later
      suspendedEffects.push(run);
    }
  }
  run();
}

// When state changes, resume effects
export function triggerEffects() {
  const effectsToRun = [...suspendedEffects];
  suspendedEffects.length = 0;
  effectsToRun.forEach(effectFn => effectFn());
}

